footer{
    border-top: solid 2px #9eb3ff;
    margin-top: 200px;
}

a.personalLink {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 300px;
  height: 30px;
  color: white;
  text-decoration: none;
}

a.personalLink:visited{
    color: white;
    text-decoration: none;
}
a.personalLink:hover {
    color: gray;
    background: white;
    text-decoration: none;
}
a.personalLink:active{
    color: white;
    text-decoration: none;
}
.footerDescription{
    color: black;
    width: 60%;
    border-radius: 5px;
    padding: 10px;
    background-color: white;
}
@media only screen and (max-width: 1000px){
    .footerDescription {
        width: 70%;
    }
}
@media only screen and (max-width: 700px) {
    .footerDescription {
        width: 90%;
    }
}


