  .welcome-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100vw;
    height: 100vh;
    min-height: 500px;
    min-width: 320px;
    font-size: 16px;
    background-color: #000;
    background-position: cover;
  }
  
  .welcome-inscription{
    margin: 0;
    color: #9eb3ff;
    font-size: 50px;
    letter-spacing: 5px;
    margin-bottom: 10px;
    text-align: center;
  }
  
  .welcome-category:hover {
    color: white;
    background-color: transparent;
    border-color: white;
    cursor: pointer;
  }
  
  .welcome-category {
    height: 30px;
    width: 200px;
    margin: 10px;
    padding: 10px;
    padding-bottom: 10px;
    display:inline-block;
    text-align: center;
    font-weight: 400;
    border-radius: 30px;
    border: 1px solid #9eb3ff;
    color: #9eb3ff;
    -webkit-animation: category-move .5s 1.05s ease-in-out forwards;
            animation: category-move .5s 1.05s ease-in-out forwards;
    opacity: 0;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
  }

  .welcome {
    display: grid;
    grid-template-columns: repeat(3, 250px);
    -ms-flex-pack: center;
        justify-content: center;
    justify-items: center;
    text-align: center;
    list-style-type: none;
    font-size: 1.25em;
    padding:0;
    letter-spacing: 2px;
  }
  

  .welcome-categories-wrapper{
    width: 100vw;
    height: 20vh;
  }

  @media screen and (max-width: 756px){

    .welcome-container{
      display: block;
      margin-top: 180px;
    }

    .welcome-inscription{
      font-size: 40px;
    }

    .welcome-category{
      height: 25px;
      width: 150px;
      font-size: 20px;
      padding-bottom: 15px;
    }

    .welcome{
      grid-template-columns: repeat(2, 200px);
    }

    .word1{
      width: 45%;
      text-align: right;
    }
    
    .word2 {
      width: 45%;
      text-align: left;
    }

  }
  
@media screen and (max-width: 400px){
    .welcome{
      grid-template-columns: 200px;
    }
    .welcome-inscription {
      font-size: 25px;
    }

  }

  @-webkit-keyframes move {
    0%{
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
      opacity: .5;
    }
    50%{
      -webkit-transform: translateY(20px);
              transform: translateY(20px)
    }
    80%{
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
    90%{
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
    }
    100%{
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }

  @keyframes move {
    0%{
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
      opacity: .5;
    }
    50%{
      -webkit-transform: translateY(20px);
              transform: translateY(20px)
    }
    80%{
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
    90%{
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
    }
    100%{
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }

  @-webkit-keyframes show{
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }

  @keyframes show{
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }

  .welcome-container span{
     display: inline-block;
     margin: 5px;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards;
     -webkit-animation-timing-function: linear;
             animation-timing-function: linear;
     -webkit-animation-name: show;
             animation-name: show;
     -webkit-animation-duration: .05s;
             animation-duration: .05s;
     opacity: 0;
  }

  .word1{
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
  }

  .word2 {
   -webkit-animation-delay: .4s;
           animation-delay: .4s;
  }

  .word3 {
   -webkit-animation-delay: .6s;
           animation-delay: .6s;
  }

  .word4 {
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
  }

  .word5 {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
  }

  @-webkit-keyframes category-move{
    0%{
      opacity: 0;
      -webkit-filter: brightness(2);
              filter: brightness(2);
    }
    100%{
      opacity: 1;
    }
  }

  @keyframes category-move{
    0%{
      opacity: 0;
      -webkit-filter: brightness(2);
              filter: brightness(2);
    }
    100%{
      opacity: 1;
    }
  }

  .element-0{
    -webkit-animation-delay: 1.05s;
            animation-delay: 1.05s;
  }

  .element-1 {
    -webkit-animation-delay: 1.15s;
            animation-delay: 1.15s;
  }

  .element-2 {
    -webkit-animation-delay: 1.25s;
            animation-delay: 1.25s;
  }

  .element-3 {
    -webkit-animation-delay: 1.15s;
            animation-delay: 1.15s;
  }

  .element-4 {
    -webkit-animation-delay: 1.25s;
            animation-delay: 1.25s;
  }

  .element-5 {
    -webkit-animation-delay: 1.35s;
            animation-delay: 1.35s;
  }

  .element-0:hover{
    color: rgb(252, 211, 30);
    border-color: rgb(252, 211, 30);
  }

  .element-1:hover {
    color: rgb(255, 0, 0);
    border-color: rgb(255, 0, 0);
  }

  .element-2:hover {
    color: rgb(61, 216, 255);
    border-color: rgb(61, 216, 255);
  }

  .element-3:hover {
    color: rgb(255, 255, 255);
    border-color: rgb(255, 255, 255);
  }

  .element-4:hover {
    color: rgb(174, 53, 255);
    border-color: rgb(174, 53, 255);
  }

  .element-5:hover {
    color: rgb(9, 255, 0);
    border-color: rgb(9, 255, 0);
  }



